// Element editor
.select-element-text {
  padding: 0.5rem;
  font-size: 20px;
  color: $dark-grey;
}

.edit-text-textarea {
  background-color: transparent;
  border: 1px solid $grey;
  outline: none;
  resize: none;
  height: 5rem;
  width: 100%;
}

.editor-text-pref-warper {
  padding: 0.5rem;

  & span {
    @include ease-rounded-corners;

    &:not(:last-child) {
      margin-inline-end: 0.5rem;
    }
  }
}

.editor-pref-warper {
  margin-block-end: 0.5rem;
}

input[type=file] {
  display: none;
}

.src-input-btn {
  font-family: Inter;
  font-size: 0.9rem;
  background-color: #FEFEFE;
  padding: 3px 6px;
  border-radius: 5px;
  border: solid 1px #06aabf;
  transition: 0.2s;

  &:hover {
    opacity: 0.8;
    cursor: pointer;
  }

  &.left {
    margin-left: -40px;
  }
}

.editor-label {
  font-size: 0.9rem;
  margin-inline-end: 1rem;
  width: 7rem;
}

.editor-indicator {
  color: $dark-grey;
  font-size: rem(12px);
  margin-inline-start: 0.3rem;
  width: 6rem;
  text-align: right;
}

input[id="color"] {
  align-self: flex-start;
  border: none;
  height: rem(24px);
  width: rem(50px);
}

.edit-select {
  width: 100%;

  &>div {
    cursor: pointer;
    @include rounded-corners;
    background-color: $white;

    &:hover {
      border-color: $black;
      background-color: $light-grey1;
      box-shadow: none;
    }

    & :last-child {
      color: $grey;

      &:hover {
        color: $dark-grey;
      }
    }
  }
}

.edit-img-src {
  margin: 0.5rem;

}

.video-edit-container,
.map-edit-container {
  margin: 0.5rem;

  & input {
    border: 1px solid $black;
    @include rounded-corners;
    background-color: transparent;
    padding: 0.5rem;
    margin-block-end: 0.5rem;
  }

  & button {
    margin: 0 auto;
    width: 5rem;
  }
}