* {
  box-sizing: border-box;
}

html {
  font-size: rem(16px);
}

body {
  background-color: $white;
  color: $black;
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

[contenteditable] {
  outline: none;
}